.loader {
  border: 16px solid #144343; /* Light grey */
  border-top: 16px solid #ec4712; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  top:40%;
  left:45%;
  position: fixed;
  transform: translate(-50%,-50%);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.admin-graphs {
  width: 500px;
  height: 500px;
}
.admin-cards {
  display: flex;
}
.admin-grid {
  display: grid;
  grid-template-columns: auto auto;
  justify-content: center;
  align-items: center;
  place-items: center;
  width: 100%;
}
@media screen and (min-width: 1200px) {
  .container {
    width: 100vw !important;
    max-width: 100vw !important;
  }
}
.column-alt {
  float: left;
  width: 33.3%;
  margin-bottom: 16px;
  padding: 0 8px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: 8px;
  width: 25%;
}
.card-blog {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: 8px;
  width: 90%;
  height: 100%;
}
.about-section {
  padding: 50px;
  text-align: center;
  background-color: #144343;
  color: #fff;
}

.container-alt {
  padding: 0 16px;
}
.card-blog {
  cursor: pointer;
}
.row {
  margin: 0px !important;
}
.container-alt::after,
.row::after {
  content: "";
  clear: both;
  display: table;
}

.title {
  color: grey;
}

.button {
  /* border: none; */
  outline: 0;
  display: inline-block;
  padding: 8px;
  /* color: white; */
  background-color: #fff;
  border: 1px solid #ec4712;
  text-align: center;
  cursor: pointer;
  width: 100%;
}

.button:hover {
  background-color: #ec4712;
  color: #fff;
}
.btn-primary {
  background-color: #ec4712;
  color: #fff;
  border: none;
}
@media screen and (max-width: 650px) {
  .column {
    width: 100%;
    display: block;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    width: 98vw !important;
    max-width: 98vw !important;
  }
}
