.header {
  height: 60px;
  width: 100%;
  position: absolute;
  z-index: 99;
  background-color: #fafafa;
  display: flex;
  align-items: center;
  top: 0px;
  left: 0px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
}
.head-nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
}
.header a {
    outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-width: 100px;
  width: fit-content;
  text-decoration: none;
  color: #000;
}
.login-btn{
    border:1px solid #e74c3c;
    width: 250px;
    padding: 8px;
}
.header a:hover {
  height: 100%;
  background-color: #e74c3c;
  color: #fafafa !important;
}
.brand {
  height: 50px;
  width: 50px;
  margin: auto 20px;
}
.user-greet {
  font-weight: 600;
  font-family: sans-serif;
  margin: auto 20px auto auto;
}

.hero {
  height: 100vh;
  width: 100%;
  padding-top: 80px;
  position: relative;
  background: url("../components/assets/hero-ban.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.hero button {
  height: 100%;
  width: 200px;
  background-color: #2ecc71;
  color: white;
  outline: none;
  border: 0px;
  cursor: pointer;
}
.input-holder {
  width: 70%;
  display: flex;
  justify-content: center;
  margin-top: 100px;
}
.hero h1 {
  font-size: 60px;
  color: #fafafa;
  -webkit-text-stroke: 2px #303030;
}
.hero input {
  width: 40%;
  height: 50px;
}
.footer {
  font-size: 25px;
  bottom: 0px;
  position: relative;
  left: 0px;
  display: flex;
  align-items: center;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
  height: 300px;
  padding: 0px 50px 0px 50px;
  width: 100%;
  color: #303030;
  font-weight: 600;
  font-family: sans-serif;
  background-color: #fff;
  justify-content: space-between;
}
.footer li {
  list-style-type: none;
}
.foot-brand {
  height: 200px;
  width: 200px;
  border-radius: 10px;
  object-fit: contain;
  background-color: #fafafa;
}
.not-found{
  margin: auto;
  width: 60vw;
  height: 100%;
}